import React, {Component} from "react";
import FixTvFilteredView from "eshop/modules/fix/components/FixTvFilteredView"
import {Expander, Section, Toggle, Trigger} from "eshop/modules/core/components/ui/Expander";


export default class FixTvFilteredExpander extends React.Component {
    getSectionHeader() {
        return (
            <div className={"u-padding-all-l u-large-padding-l-xl  u-padding-left-xxl u-box-shadow--s u-small-padding-xl "}>
            <Trigger
                className="js-expander__trigger__nested opl-section__expander-trigger u-inline-block  u-margin-left-l u-margin-right-l u-margin-top-m">

                <div className="u-padding-left-xl u-padding-top-m u-padding-right-xl ">

                        <span className="js-expander__trigger--hide "><h4 className={"u-padding-top-s"}>Wyszukaj kanały lub pakiety telewizyjne</h4></span>
                        <span className="js-expander__trigger--show"><h4 className={"u-padding-top-s"}>Wyszukaj kanały lub pakiety telewizyjne</h4></span>

                </div>
            </Trigger>
            </div>
        );
    }

    render() {
        if(!!this.props.tvPackages && this.props.tvPackages.length>0){
        return (



            <Section header={this.getSectionHeader()}
                     contentClassName="js-expander__container__nested"
                     headerClassName=" u-small-text-right opl-section--togglable u-margin-top"
                     className="u-position-relative opl-section opl-section--togglable  u-no-margin u-no-padding-top "
                     expanded={false}
            >
                <FixTvFilteredView tvPackages={this.props.tvPackages}></FixTvFilteredView>
            </Section>


        )}
        return (null);
    }
}


